import React from "react"
import { Empty, Heading, Button, Card } from "components"
import { Trans } from "@lingui/macro"
import { Col, Row, List, Typography } from "antd"
import { mobile, media } from "helpers"
import { country } from "App"
import { Link } from "react-router-dom"
import styled from "styled-components"
import decorating from "assets/images/decorating.png"
import Avatar from "components/Avatar"
import moment from "moment"

const BlurredCard = styled(Card)`
	&& {
		width: 60%;
		height: auto;
		max-height: 240px;
		left: 50%;
		position: absolute;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: 2;
		&:hover {
			transform: translate(-50%, -50%);
		}

		${media.mobile`
            width: 100%;
            height: auto;
        `}
	}
`

const StyledList = styled(List).attrs(() => ({
	noHover: true,
}))`
	filter: ${({ token }) => !token && "blur(6px)"};
	user-select: ${({ token }) => !token && "none"};
	.ant-list-item {
		box-shadow: none;
		border-radius: 0;
	}
`

export default function RenderTestimonials({ tutorTestimonials, unauthorizedUser, testimonials }) {
	const token = localStorage.getItem("token")

	const render = () => {
		if (!token)
			return (
				<>
					<BlurredCard autoHeight noHover>
						<Row type="flex" justify="center" align="middle">
							<Col lg={12} xs={24}>
								<img
									src={decorating}
									width={mobile ? "100%" : "130"}
									alt={<Trans>Register first to see testimonials</Trans>}
								/>
							</Col>
							<Col lg={12} xs={24}>
								<Heading
									level={4}
									content={<Trans>Login to see</Trans>}
									subheader={
										<Trans>You can see testimonials of this tutor, if you are already logged in. Go login now.</Trans>
									}
									marginBottom="2em"
								/>
								<Link to={`/${country}/login`}>
									<Button type="primary">
										<Trans>Login now</Trans>
									</Button>
								</Link>
							</Col>
						</Row>
					</BlurredCard>
					<StyledList data={tutorTestimonials} token={token} type="review" />
				</>
			)

		return (
			<StyledList
				dataSource={testimonials}
				locale={{
					emptyText: (
						<Empty
							description={
								unauthorizedUser ? (
									<Trans>This tutor has no testimonials yet</Trans>
								) : (
									<Trans>You haven't gained any testimonials yet</Trans>
								)
							}
						/>
					),
				}}
				token={token}
				renderItem={(item) => {
					const reviewer = item.reviewer.firstName + " " + item.reviewer.lastName
					return (
						<List.Item>
							<List.Item.Meta
								avatar={<Avatar src="" />}
								title={
									<Typography>
										{reviewer}{" "}
										{item.rating && (
											<Typography.Text type="secondary">
												gives you rating of <strong className="primary">{item.rating}</strong>
											</Typography.Text>
										)}{" "}
										&middot; <Typography.Text type="secondary">{moment(item.postedDate).fromNow()}</Typography.Text>
									</Typography>
								}
								description={item.comment}
							/>
						</List.Item>
					)
				}}
			/>
		)
	}

	return <div>{render()}</div>
}
